import nicemodel from '../../components/common/nicemodel.vue'
import {
	mapState,
	mapActions
} from 'vuex'
export default {
	name: "",
	components: {
		nicemodel
	},
	props: {},
	data() {
		return {
			// 分页
			page: 1
		}
	},
	methods: {
		...mapActions(['getTopics']),
	},
	mounted() {
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 300
		})
		uni.showLoading({
			title: '加载中...'
		});
		this.getTopics(this.page)
	},
	onShow() {},
	onLoad() {

	},
	onReachBottom() {
		if (this.topicArr.length === 20) {
			uni.showToast({
				title: '暂无更多资源~',
				icon: 'none'
			});
		} else {
			uni.showLoading({
				title: '加载中...'
			});
			this.page++
			this.getTopics(this.page)
		}
	},
	filters: {

	},
	computed: {
		...mapState(['topicArr']),
	},
	watch: {},
	directives: {

	}
}
